import { initializeApp } from "firebase/app";
import {
	createUserWithEmailAndPassword,
	getAuth,
	updateProfile,
} from "firebase/auth";
import { getFirestore, setDoc, doc, arrayUnion } from "firebase/firestore";

const firebaseConfig = {
	apiKey: "AIzaSyBngS1-OkcpbVlqLFlx2DkcTTNd5sVLupw",
	authDomain: "bookshelf-c1a52.firebaseapp.com",
	projectId: "bookshelf-c1a52",
	storageBucket: "bookshelf-c1a52.appspot.com",
	messagingSenderId: "981091065975",
	appId: "1:981091065975:web:b7b676ab91ab2b855ce672",
};

let app = initializeApp(firebaseConfig);
const db = getFirestore(app);
const auth = getAuth(app);

const registerUser = async (email, password, userName) => {
	await createUserWithEmailAndPassword(auth, email, password)
		.then(() => {
			console.log(auth);
			setNewUserDb(auth.currentUser.uid, userName);
		})
		.catch((error) => {
			console.log(error);
		});
};

const setNewUserDb = async (uid, userName) => {
	await setDoc(doc(db, "users", uid), {
		allBookIds: [],
	})
		.then(() => {
			console.log("user db added");
			setDisplayName(userName);
		})
		.catch((error) => {
			console.log(error);
		});
};

const setDisplayName = (userName) => {
	updateProfile(auth.currentUser, {
		displayName: userName,
	});
};

const addBookId = async (bookId, uid) => {
	await setDoc(
		doc(db, "users", uid),
		{
			allBookIds: arrayUnion(bookId),
		},
		{ merge: true }
	)
		.then(() => {
			console.log("book id added");
		})
		.catch((error) => {
			console.log(error);
		});
};

const addBookToDb = async (data, uid) => {
	await setDoc(
		doc(db, "users", uid, "allBooks", data.id),
		{
			data,
		},
		{ merge: true }
	)
		.then(() => {
			addBookId(data.id, uid);
			console.log("book added");
		})
		.catch((error) => {
			console.log(error);
		});
};

const updateBookInDb = async (data, uid) => {
	await setDoc(
		doc(db, "users", uid, "allBooks", data.id),
		{
			data,
		},
		{ merge: true }
	)
		.then(() => {
			console.log("book updated");
		})
		.catch((error) => {
			console.log(error);
		});
};

export { db, auth, registerUser, addBookToDb, updateBookInDb };
